/* From 98.css */
ul.tree-view {
    display: block;
    background: #fff;
    box-shadow: inset -1px -1px #fff, inset 1px 1px grey, inset -2px -2px #dfdfdf, inset 2px 2px #0a0a0a;
    padding: 6px;
    margin: 0;}

/* From 98.css */
ul.tree-view a:focus {
    background-color: navy;
    color: #fff;}

/* From 98.css */
ul.tree-view ul > li:last-child:after {
    content: "";
    display: block;
    position: absolute;
    left: -20px;
    top: 7px;
    bottom: 0;
    width: 8px;
    background: #fff;}

/* From 98.css */
ul.tree-view details > summary:before {
    text-align: center;
    display: block;
    float: left;
    content: "+";
    border: 1px solid grey;
    width: 8px;
    height: 9px;
    line-height: 8px;
    margin-right: 5px;
    padding-left: 1px;
    background-color: #fff;}

/* From 98.css */
pre {
    display: block;
    background: #fff;
    box-shadow: inset -1px -1px #fff, inset 1px 1px grey, inset -2px -2px #dfdfdf, inset 2px 2px #0a0a0a;
    padding: 12px 8px;
    margin: 0;}

/* From index.css */
.typography--normaltext {
    background-color: white;
    padding: 4px;
    font-size: 12px;
    font-family: "Arial", -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif !important;
    box-shadow: var(--inset__inverse);}

/* From index.css */
.text--highlighted {
    text-decoration: underline;}

/* From index.css */
hr.hr--accent {
    border: 0;
    height: 1px;
    background: linear-gradient(90deg, red, blue);}

/* From index.css */
hr.hr--accent2 {
    border: 0;
    height: 1px;
    background: linear-gradient(90deg, green, blue);}

/* From prog.css */
.polaroid {
  background: #f8f7f6;
  padding: 1rem;
  box-shadow: 7px 7px 2px 1px rgba(0, 15, 6, 0.2);
  border-radius: 10px;}

/* From prog.css */
.bub-part-a,
.bub-part-b,
.bub-part-c {
  height: 2px;
  background: var(--Speech-Bubble-Border);}

/* From prog.css */
.bub-part-b,
.bub-part-c {
  background: var(--Speech-Bubble-Background);
  border-left: 2px solid var(--Speech-Bubble-Border);
  border-right: 2px solid var(--Speech-Bubble-Border);}

/* From prog.css */
.speech-txt {
  padding: 10px;
  background: var(--Speech-Bubble-Background);
  border-left: 2px solid var(--Speech-Bubble-Border);
  border-right: 2px solid var(--Speech-Bubble-Border);
  font-size: 15px;
  text-align: center;
  width: 100%;
  box-sizing: border-box;
  word-wrap: break-word;
  font-family: 'Millennium';}

/* From prog.css */
.pixel-speech-bubble-arrow::after {
  content: '';
  position: absolute;
  top: -7px;
  left: 2px;
  width: 0;
  height: 0;
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-right: 8px solid var(--Speech-Bubble-Background);}

